<template>
  <v-container>
    <h5 class="blue--text">Welcome to {{heading}}:</h5>
    <p>This module allows to create a new challenge or view existing ones: <v-icon large color="blue darken-4">mdi-flag</v-icon></p>
    <v-tabs v-model="tab" grow>
        <v-tab href="#newc">New</v-tab>
        <v-tab href="#viewc">View</v-tab>
        <v-tab-item value="newc" class="ma-5">
            <p>Please fill all the required details below to start a new Challenge:</p>
            <v-form ref="newcform">
              <v-text-field v-model="title" :counter="10" label="Challenge Name" :rules="nameRules" required></v-text-field>
              <v-select v-model="type" :items="items" label="Type" required></v-select>
              <v-text-field v-model="target" label="Select the list of people you wanted to challeng" required></v-text-field>
              <v-text-field v-model="target" label="Enter your target"  required></v-text-field>
              <v-file-input v-model="media" label="Select a Media/Image"  required></v-file-input>         
              <div class="text-center pb-8">
                <p v-if="success" class="green">A new challenge has been added and you can view from View tab.</p>
                  <v-btn class="primary mr-4" @click="addNewChallenge">Submit</v-btn>
                  <v-btn class="primary">Cancel</v-btn>
              </div>
            </v-form>

        </v-tab-item>
        <v-tab-item value="viewc">
        <p>Here are the list of your previous challenges:</p>
        <v-card v-for="challenge in challenges" :key="challenge.title" class="mb-10" outlined>
          <v-img v-if="challenge.media" class="green--text" height="200px" :src="require(`../assets/${challenge.media}`)">
            <v-card-title class="align-end fill-height"><strong>{{challenge.title}}</strong></v-card-title>
          </v-img>
          <v-card-title v-else>{{challenge.title}}</v-card-title>

          <v-card-text>{{challenge.description}}</v-card-text>
          <v-card-actions>
            <v-btn class="ma-2" text icon color="blue lighten">
              <v-icon>mdi-thumb-up</v-icon>
            </v-btn>
            <v-btn class="ma-2" text icon color="blue lighten">
              <v-icon>mdi-share-variant</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>              
        </v-tab-item>
    </v-tabs>
  </v-container>
</template>

<script>
export default {
  name: 'Challenges',
    data: () => ({
         heading: 'Challenges',
         success: false,
         items: ['Personal', 'Team'],
         challenges: [
            { title: 'August Plastic Challenge', type: 'Team', media: 'no-plastic.png', description: 'This challenge is reduce the plastic usage 50% among our team.', status: 'Completed'},
            { title: 'September Rideshare Challenge', type: 'Personal', media: 'pubtrans.png', description: 'Will use public tranportation for whole month September.', status: 'Completed'}
         ],
         nameRules: [
           v => v.length >=3 || 'Minmum Length is 3 Characters'
         ]  
    }),
    methods: {
        addNewChallenge: function() {
            if(this.$refs.newcform.validate()){
              this.challenges.push({ title: this.title, type: this.type, status: 'New'});
              this.success = true;
            }

        },
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  margin-top: 60px;
}
</style>