<template>
  <v-layout justify-center align-center>
    <v-flex md12 fill-height>
      <v-card width="100%" style="min-height: calc(100vh - 100px)">
        <v-card-title class="justify-center" style="height:70px">
          <span style="font-size: 14px; text-align: center">Carbon Offsets</span>
        </v-card-title>
        <v-divider />
        <v-tabs v-model="tab" background-color="transparent" grow>
          <v-tab
            style="font-size: 14px; text-transform:none"
            v-for="item in items"
            :key="item"
          >{{ item }}</v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
          <v-tab-item>
            <v-container fluid>
              <v-row>
                <v-col cols="6">
                  <v-card
                    height="100"
                    color="amber lighten-2"
                    dark
                    @click="selectedCategory='airplane travel'"
                  >
                    <v-card-text
                      class="white--text"
                      style="position: relative; top:20%; text-align: center"
                    >
                      <v-icon>fas fa-plane</v-icon>
                      <br />Airplane travel
                    </v-card-text>
                  </v-card>
                </v-col>
                <v-col cols="6">
                  <v-card
                    height="100"
                    color="cyan lighten-1"
                    dark
                    @click="selectedCategory='car travel'"
                  >
                    <v-card-text
                      class="white--text"
                      style="position: relative; top:20%; text-align: center"
                    >
                      <v-icon>fas fa-car</v-icon>
                      <br />Car travel
                    </v-card-text>
                  </v-card>
                </v-col>
                <v-col cols="6">
                  <v-card
                    height="100"
                    color="green accent-3"
                    dark
                    @click="selectedCategory='shipping'"
                  >
                    <v-card-text
                      class="white--text"
                      style="position: relative; top:20%; text-align: center"
                    >
                      <v-icon>fas fa-shipping-fast</v-icon>
                      <br />Shipping
                    </v-card-text>
                  </v-card>
                </v-col>
                <v-col cols="6">
                  <v-card
                    height="100"
                    color="blue-grey darken-1"
                    dark
                    @click="selectedCategory='electricity'"
                  >
                    <v-card-text
                      class="white--text"
                      style="position: relative; top:20%; text-align: center"
                    >
                      <v-icon>fas fa-lightbulb</v-icon>
                      <br />Electricity
                    </v-card-text>
                  </v-card>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <v-card height="300" v-if="selectedCategory" flat>
                    <v-card-text>
                      <v-form>
                        <v-row>
                          <v-col cols="6">
                            <v-text-field v-model="selectedAmount" v-bind:label="unitLabel"></v-text-field>
                          </v-col>
                          <v-col cols="6">
                            <v-select
                              height="15"
                              :items="recurrence"
                              v-model="selectedRecurrence"
                              label="Recurrence"
                              outlined
                            ></v-select>
                          </v-col>
                        </v-row>
                        <v-row>
                          <v-col cols="12">
                            <v-dialog
                              v-if="selectedRecurrence=='One-time'"
                              ref="dialog"
                              v-model="modal"
                              :return-value.sync="selectedDate"
                              persistent
                              width="290px"
                            >
                              <template v-slot:activator="{ on }">
                                <v-text-field
                                  v-model="selectedDate"
                                  label="Scheduled Date"
                                  readonly
                                  v-on="on"
                                ></v-text-field>
                              </template>
                              <v-date-picker v-model="selectedDate" scrollable>
                                <div class="flex-grow-1"></div>
                                <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                                <v-btn
                                  text
                                  color="primary"
                                  @click="$refs.dialog.save(selectedDate)"
                                >OK</v-btn>
                              </v-date-picker>
                            </v-dialog>
                          </v-col>
                        </v-row>
                        <v-row v-if="computedOffset">
                          <v-col cols="4">
                            <v-chip color="cyan" dark label height="30">
                              <v-icon medium color="white">fas fa-comment-dollar</v-icon>
                              <span style="font-size: 15px; margin-left: 10px">{{computedOffset}}</span>
                            </v-chip>
                          </v-col>
                          <v-col cols="2">
                            <v-btn
                              outlined
                              x-small
                              fab
                              color="cyan"
                              @click.stop="submittedDialog = true"
                            >
                              <v-icon>fas fa-plus</v-icon>
                            </v-btn>
                          </v-col>
                          <v-col cols="2">
                            <v-btn outlined x-small fab color="red">
                              <v-icon>fas fa-times</v-icon>
                            </v-btn>
                            <v-dialog v-model="submittedDialog" max-width="290">
                              <v-card>
                                <v-card-text style="padding-top: 30px;  font-size: 15px">
                                  Added {{selectedRecurrence}} offset of ${{computedOffset}} for {{selectedCategory}}
                                  <span
                                    v-if="selectedRecurrence=='One-time'"
                                  >
                                    <br />
                                    <br />
                                    Scheduled Date: {{selectedDate}}
                                  </span>
                                </v-card-text>

                                <v-card-actions>
                                  <div class="flex-grow-1"></div>

                                  <v-btn
                                    color="green darken-1"
                                    text
                                    @click="submitOffset(); clearForm()"
                                  >OK</v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-col>
                        </v-row>
                      </v-form>
                    </v-card-text>
                  </v-card>
                  <v-card height="300" v-if="selectedCategory == null" flat>
                    <v-list dense>
                      <v-subheader style="font-size:14px">Recently Created</v-subheader>
                      <v-divider></v-divider>
            
                        <v-list-item v-for="(item, i) in offsets" :key="i">
                          <v-list-item-icon>
                            <v-icon v-text="item.icon"></v-icon>
                          </v-list-item-icon>
                          <v-list-item-content>
                            <v-list-item-title>${{item.amount}}</v-list-item-title>
                          </v-list-item-content>
                        </v-list-item>
                        <v-divider inset></v-divider>
                   
                    </v-list>
                  </v-card>
                </v-col>
              </v-row>
            </v-container>
          </v-tab-item>
          <v-tab-item>
            <v-card flat>
              <v-list dense>
                <v-subheader style="font-size:14px">Recurring Payments</v-subheader>
                <v-divider></v-divider>
               
                  <v-list-item v-for="(item, i) in recurrentOffsets" :key="i">
                    <v-list-item-icon>
                      <v-icon style="padding-top:5px" v-text="item.icon"></v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>${{item.amount}}</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-action>
                      <v-btn icon @click="deleteOffset(i)">
                        <v-icon small>fas fa-trash</v-icon>
                      </v-btn>
                    </v-list-item-action>
                  </v-list-item>
                  <v-divider inset></v-divider>
            
              </v-list>
            </v-card>
          </v-tab-item>
          <v-tab-item>
            <v-card flat>
              <v-list dense>
                <v-subheader style="font-size:14px">Historical Payments</v-subheader>
                <v-divider></v-divider>
              
                  <v-list-item v-for="(item, i) in transactions" :key="i">
                    <v-list-item-icon>
                      <v-icon style="padding-top:5px" v-text="item.icon"></v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>${{item.amount}}</v-list-item-title>
                      <v-list-item-subtitle v-text="item.offsetDate"></v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider inset></v-divider>
          
              </v-list>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
export default {
  data() {
    return {
      tab: null,
      selectedCategory: null,
      selectedAmount: null,
      selectedRecurrence: null,
      selectedDate: null,
      modal: false,
      submittedDialog: false,
      items: ["New", "Recurring", "Transaction History"],
      recurrence: ["Weekly", "Monthly", "One-time"],
      categories: ["airplane", "car", "shipping", "electricity"],
      offsets: [
        {
          category: "airplane travel",
          icon: "fas fa-plane",
          amount: "0.50",
          offsetDate: "2019-05-31",
          offsetRecurrence: "One-time"
        },
        {
          category: "car travel",
          icon: "fas fa-car",
          amount: "0.30",
          offsetDate: "2019-07-31",
          offsetRecurrence: "Weekly"
        }
      ],
      transactions: [
        {
          category: "airplane travel",
          icon: "fas fa-plane",
          amount: "0.50",
          offsetDate: "2019-05-31",
          offsetRecurrence: "One-time"
        },
        {
          category: "car travel",
          icon: "fas fa-car",
          amount: "0.30",
          offsetDate: "2019-07-31",
          offsetRecurrence: "One-time"
        }
      ]
    };
  },
  computed: {
    // a computed getter
    unitLabel: function() {
      if (this.selectedCategory == "airplane travel") {
        return "Air Miles";
      }
      if (this.selectedCategory == "car travel") {
        return "Car Miles";
      }
      if (this.selectedCategory == "shipping") {
        return "Shipping Miles";
      }
      if (this.selectedCategory == "electricity") {
        return "KW/H";
      } else return "";
    },
    computedOffset: function() {
      if (this.selectedAmount && !isNaN(this.selectedAmount)) {
        return (this.selectedAmount * 0.00013).toFixed(2);
      } else {
        return null;
      }
    },
    recurrentOffsets: function() {
      return this.offsets.filter(function(o) {
        return o.offsetRecurrence !== "One-time";
      });
    }
  },
  methods: {
    clearForm: function() {
      this.submittedDialog = false;
      this.selectedAmount = null;
      this.selectedRecurrence = null;
      this.selectedDate = null;
      this.selectedCategory = null;
    },
    deleteOffset: function(i) {
      this.offsets.splice(i, 1);
    },
    submitOffset: function() {
      var myIcon = "";
      var category = this.selectedCategory;
      if (category === "airplane travel") {
        myIcon = "fas fa-plane";
      } else if (category === "car travel") {
        myIcon = "fas fa-car";
      } else if (category === "shipping") {
        myIcon = "fas fa-shipping-fast";
      } else if (category === "electricity") {
        myIcon = "fas fa lightbulb";
      }
      var newOffset = {
        category: this.selectedCategory,
        icon: myIcon,
        amount: this.selectedAmount,
        offsetDate: this.selectedDate,
        offsetRecurrence: this.selectedRecurrence
      };
      this.offsets.push(newOffset);
    }
  }
};
</script>
<style>
.v-input .v-label .v-list-item {
  font-size: 0.5em;
}
v-input__slot {
  size: 10px;
}
.v-input__control {
  size: 10px;
}
</style>