<template>
  <div>
    <pie-chart :data="chartData" :options="chartOptions" :styles="myStyles"></pie-chart>
  </div> 
</template>

<script>
import PieChart from "../PieChart.js";
export default {
  name: 'ScoreBoard',
  components: {
    PieChart
  },
  data() {
    return {
      chartOptions: {
        tooltip: true,
        hoverBorderWidth: 5,
        cutoutPercentage: 80,
        rotation: 180,
        responsive: true, 
        maintainAspectRatio: false,
        title: {
          display: true,
          position: "bottom",
          text: "Sustainability Score"
        }
      },
      chartData: {
        hoverBackgroundColor: "white",
        hoverBorderWidth: 20,
        labels: ["CarbonOffset", "PlasticUsage", "GreenCulture","Diet"],
        datasets: [
          {
            label: "Score",
            backgroundColor: ["#41B883", "#E46651", "#00D8FF","#00A920"],
            data: [104, 10, 15, 46]
          }
        ]
      }
    };
  },
  computed: {
    myStyles () {
      return {
        height: '50%',
        position: 'relative'
      }
    }
  },
};
</script>

<style scoped>
  #scoreBoard {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 10px;
  }
  .small {
      max-width: 600px;
      margin:  100px auto;
  }
</style>