<template>
<v-app>
<v-container fill-height fluid pa-0 style="background-color: #EEEEEE"
        flat>
    <v-app-bar flat dense hide-on-scroll absolute min-width="100%" height="50"  scroll-target="#scrolling"> 
    
     <v-app-bar-nav-icon><v-img contain src="@/assets/worldwide.svg" height="50" width="40"/></v-app-bar-nav-icon>
      <v-toolbar-title ><span class="titl">Hello Earth</span></v-toolbar-title>

      <div class="flex-grow-1" id="scrolling"></div>

      <v-avatar size="40px" @click="toggleActive('dashboard', $event)">
        <img
          src="https://sundaydigital.com.au/wp-content/uploads/2019/04/sample-avatar-002.jpg"
        >
      </v-avatar>
    </v-app-bar>

        <v-content >
            
    
            <router-view></router-view>
       
        
       
       
        </v-content>
      <v-bottom-navigation flat height="50" width="100%"
      :value="activeBtn"
      absolute
    >
  
     <v-img v-bind:style = "(activeBtn == 'dashboard')? '': 'filter:grayscale(100%) opacity(50%);'" @click="toggleActive('dashboard', $event)" contain src="@/assets/data.svg" height="45" width="40"/>
     <v-img v-bind:style = "(activeBtn == 'rideshare')? '': 'filter:grayscale(100%) opacity(50%);'" @click="toggleActive('rideshare', $event)" contain src="@/assets/car.svg" height="60" width="60"/>
     <v-img v-bind:style = "(activeBtn == 'challenges')? '': 'filter:grayscale(100%) opacity(50%);'" @click="toggleActive('challenges', $event)" contain src="@/assets/adventurer.svg" height="50" width="60"/>
     <v-img v-bind:style = "(activeBtn == 'carbonoffset')? '': 'filter:grayscale(100%) opacity(50%);'" @click="toggleActive('carbonoffset', $event)" contain src="@/assets/carbon-dioxide.svg" height="50" width="50"/>
     <v-img v-bind:style = "(activeBtn == 'marketplace')? '': 'filter:grayscale(100%) opacity(50%);'" @click="toggleActive('marketplace', $event)" contain src="@/assets/commerce.svg" height="50" width="50"/>


  
    </v-bottom-navigation>
       </v-container>
    </v-app>
</template>

<script>
export default {
    name: 'App',
    data () {
      return {
        activeBtn: 'dashboard',
      }
    },
    computed: {
        // a computed getter
        pageTitle: function () {
            var routeToTitle = { "dashboard" : "Dashboard", "rideshare" : "Rideshare", "challenges": "Challenges", "carbonoffset": "Carbon Offsets", "marketplace": "Marketplace"};
            return routeToTitle[this.activeBtn];
        }
    },
    methods: {
      toggleActive: function(val) {
        if (this.activeBtn == val) return;
        this.activeBtn = val;
        return this.$router.push(val);
      }
    }
};
</script>


<style>
.titl {
  font-family: Notable;
  font-size:14px;
  color: #5C6BC0;
}
</style>